  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
}

.link-menu {
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  position: relative;
}

.link-menu:hover, .link-menu:active, .link-menu:focus {
  opacity: .8;
  color: var(--color-red);
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
}

.link-menu:after {
  content: "";
  background: none;
  width: 0;
  height: 3px;
  margin: auto;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  display: block;
  position: absolute;
  top: 16px;
}

.link-menu:hover:after, .link-menu.active-nav:after {
  background: var(--color-red);
  width: 100%;
}

.active-link-menu:after {
  content: "";
  background: var(--color-red);
  width: 100%;
  height: 3px;
  margin: auto;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  display: block;
  position: absolute;
  top: 16px;
}

.search-form {
